import * as React from 'react';
import * as T from 'prop-types';
import styled from 'styled-components';
import DocExample from '@splunk/react-docs/DocExample';
import P from '@splunk/react-ui/Paragraph';
import Heading from '@splunk/react-ui/Heading';
import List from '@splunk/react-ui/List';
import Chip from '@splunk/react-ui/Chip';
import ChartLine from '@splunk/react-icons/ChartLine';
import ChartBar from '@splunk/react-icons/ChartBar';
import ChartScatter from '@splunk/react-icons/ChartScatter';
import ChoroplethMap from '@splunk/react-icons/ChoroplethMap';
import SplitBy from '@splunk/react-icons/SplitBy';
import { Overview, Variation } from './interfaces/Overview';
import { VizCategory } from './interfaces/VizCategory';

interface VariationCode {
    code: string;
    example: React.ReactElement;
}

interface OverviewProps {
    overview: Overview;
    variationCode: Record<string, VariationCode>;
}

const chipIconStyles = {
    color: 'white',
};

const chipType = {
    [VizCategory.TRENDS]: {
        backgroundColor: '#66AAF9',
        icon: <ChartLine style={chipIconStyles} />,
    },
    [VizCategory.COMPARISONS]: {
        backgroundColor: '#45D4BA',
        icon: <ChartBar style={chipIconStyles} />,
    },
    [VizCategory.CORRELATION]: {
        backgroundColor: '#FB865C',
        icon: <ChartScatter style={chipIconStyles} />,
    },
    [VizCategory.SPATIAL]: {
        backgroundColor: '#E85B79',
        icon: <ChoroplethMap style={chipIconStyles} />,
    },
    [VizCategory.FLOW]: {
        backgroundColor: '#9980FF',
        icon: <SplitBy style={chipIconStyles} />,
    },
};

const StyledChip = styled(Chip)`
    [data-test='label'] {
        color: white;
    }
    margin-left: 10px;
    margin-bottom: 10px;
`;

const Variations = ({ variations, variationCode }): React.ReactElement => (
    <>
        {variations.map(
            (v: Variation, i: number): React.ReactElement => {
                const variationObj = variationCode[v.key];
                return <DocExample key={i} code={variationObj.code} example={variationObj.example} {...v} />;
            }
        )}
    </>
);

Variations.propTypes = {
    variations: T.array,
    variationCode: T.object,
};

const Tip = ({ tip }): React.ReactElement => <List.Item>{tip}</List.Item>;

Tip.propTypes = {
    tip: T.string,
};

const Tips = ({ tips }): React.ReactElement => (
    <>
        <Heading level={3}>Tips</Heading>
        <List>
            {tips.map(
                (tip: string, i: number): React.ReactElement => (
                    <Tip key={i} tip={tip} />
                )
            )}
        </List>
    </>
);

Tips.propTypes = {
    tips: T.array,
};

const OverviewDoc = (props: OverviewProps): React.ReactElement => {
    const { typeOfViz, overview, tips, variations } = props.overview;
    const variationCode = props.variationCode;
    const vizTypeDisplayNameMap = {
        [VizCategory.TRENDS]: 'Trend',
        [VizCategory.COMPARISONS]: 'Comparison',
    };
    const vizType = vizTypeDisplayNameMap[typeOfViz] || typeOfViz;
    return (
        <>
            {vizType && (
                <>
                    Chart Type
                    <StyledChip
                        data-test="viz-type-chip"
                        backgroundColor={chipType[typeOfViz].backgroundColor}
                        icon={chipType[typeOfViz].icon}
                    >
                        {vizType}
                    </StyledChip>
                </>
            )}
            <P data-test="overview-description">{overview}</P>
            <Tips data-test="tips" tips={tips} />
            {variationCode && <Variations variations={variations} variationCode={variationCode} />}
        </>
    );
};

OverviewDoc.propTypes = {
    overview: T.object,
    variationCode: T.object,
};

export default OverviewDoc;
